@font-face {
  font-family: "Amazon Ember";
  font-style: normal;
  font-weight: 100;
  src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
  src: local("Amazon Ember"), local("AmazonEmber"),
    /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf") format("truetype");
}

body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue";
}

.designForm {
  padding: 0 7vw;
  margin-bottom: 50px;
}

.designForm .section_div {
  margin-top: 30px;
}

.designForm p {
  color: #000;
  font-size: 16px;
  font-weight: 200;
  line-height: 26px;
  text-align: left;
}

.top_middle_faq {
  width: 16%;
  top: 44%;
  right: 0;
  z-index: -1;
  position: absolute;
}

.input-textN {
  margin: 5px 0 10px 0;
  padding: 2px 5px;
  width: 82%;
  font-size: 14px;
  letter-spacing: 1px;
}

.input-textN::-webkit-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-textN:-ms-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-textN::-ms-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-textN::placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-areaN {
  margin: 5px 0 5px 0;
  padding: 2px 5px;
  width: 82%;
  font-size: 14px;
  letter-spacing: 1px;
}

.input-areaN::-webkit-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-areaN:-ms-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-areaN::-ms-input-placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.input-areaN::placeholder {
  font-size: 11px;
  opacity: 0.5;
}

.image-width{
  width: 100%;
}

.file-upload {
  margin: 10px 0;
}

.nomitnateButton{
  height: 4vh;
  width: 6vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  letter-spacing: 1px;
  /* float: left; */
  /* margin-left: 2vw; */
}

.wrapperForDropZone{
  font-size: 13px;
    font-weight: 300;
    text-align: center;
    background-color: rgb(238, 238, 238, 0.3);
    border: 1px dotted rgb(153, 153, 153);
    color: rgb(102, 102, 102);
    height: 4vh;
    width: 11vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    letter-spacing: 1px;
}

.dropzone-button {

  color: #fff;
  background-color: #f8991d;
  border-color: #2e6da4;
  border-radius: 3px;
  display: inline-block;
  padding: 6px 10px;
  margin-bottom: 0;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  cursor: pointer;
  margin: 10px 0;
}

.thumbnailN{
  position: absolute;
  top: -36px;
  z-index: -1;
  right: -24px;
  width: 100%;
  color: rgb(64, 104, 137);
  border: none;
}

.errorText{
  padding: 4px 10px 4px 10px;
  font-size: 11px;
  display: inline-block;
}

.loaderMain{
  width: 100%;
  height: 100%;
  padding: 40px;
  vertical-align: middle;
  display: -ms-flexbox;
  display: flex;
  top: 0;
  z-index: 1;
  margin: auto;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.marginAutp{
  margin: auto
}

.override{
  display: block !important;
margin: 0 auto !important;
border-color: red !important;
}

.subheading{
  font-size: 18px;
}

.assetsHeading{
  font-size: 14px;
  /* text-decoration: underline; */
  margin: 0 0 8px 0;
}

.assetsBlock{
  margin: 18px 0;
}

.generalSection{
  font-size: 16px;
  text-decoration: underline;
  margin-top: 35px;
}

.rulesNote{
  font-weight: 100;
  font-size: 10px;
  color: #ef0033;
}

.assetsOptional{
  font-weight: 100;
  font-size: 10px;
  color: #525252;
}

.assetsForDropZone {
  font-size: 12px;
  font-weight: 300;
  text-align: center;
  background-color: rgb(238, 238, 238, 0.3);
  border: 1px dotted rgb(153, 153, 153);
  color: rgb(102, 102, 102);
  width: 100%;
  height: 3vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  letter-spacing: 1px;
  margin: 0 0 20px 0px;
}
.assetsForDropZone-error {
  background-color: rgba(243, 207, 207, 0.3);
  border: 1px dotted rgb(255, 0, 0);
  color: rgb(255, 0, 0);
}


.assetsUploadButton{
  background: none;
  color: rgb(0, 0, 0);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 6px 20px;
  float: left;
  text-align: center;
  font-size: 10px;
  letter-spacing: 1.4px;
  text-decoration: none;
  font-weight: 400;
  display: -ms-flexbox;
  display: flex;
}

.assetsUploadButton:disabled{
  opacity:0.3;
}

.designerUpload{
  margin: 25px 0;
}

.assetsForDropZone > div{
  overflow-wrap: break-word;
  width: inherit;
}

.underline{
  text-decoration: underline;
}

.download{
  text-decoration: underline;
  font-size: 11px;
}

@-webkit-keyframes appear {
  100% {
    opacity: 1;
  }
}

@keyframes appear {
  100% {
    opacity: 1;
  }
}


@media (min-width: 1920px) {
  .faq {
    padding: 0;
  }
}


