.season .title {
  color: #363436;
  font-size: 38px;
  font-weight: 100;
  letter-spacing: 1px;
  padding-bottom: 30px;
  width: 572px;
  text-align: right;
}
.seasonRadio {
  float: left;
  width: 154px;
  color: #363436;
  font-weight: 300;
}
.seasonRadio:hover input ~ .selectButton {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border: 4px solid #ccc;
}
.seasonRadio:hover input:checked ~ .selectButton {
  border: 0px solid #ccc;
}
.selectButton {
  position: absolute;
  top: 220px;
  height: 18px;
  width: 18px;
  border: 1px solid #ccc;
  margin-left: 23px;
}
.seasonRadio input:checked ~ .selectButton {
  background-color: #000;
  border: 0px solid #ccc;
}
.selectButton:after {
  content: "";
  position: absolute;
  display: none;
}
.seasonRadio input:checked ~ .selectButton:after {
  display: block;
}

.patternRadio {
  float: left;
  width: 127px;
  color: #363436;
  font-weight: 300;
  margin-top: 30px;
  position: relative;
  left: 104px;
}
.selectButtonPattern {
  position: absolute;
  top: 95px;
  height: 18px;
  width: 18px;
  border: 1px solid #ccc;
  margin-left: 13px;
}
.selectButtonPattern:after {
  content: "";
  position: absolute;
  display: none;
}
.patternRadio:hover input ~ .selectButtonPattern {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border: 4px solid #ccc;
}
.patternRadio:hover input:checked ~ .selectButtonPattern {
  border: 0px solid #ccc;
}

.patternRadio input:checked ~ .selectButtonPattern {
  background-color: #000;
  border: 0px solid #ccc;
}
.patternRadio input:checked ~ .selectButtonPattern:after {
  display: block;
}

