@font-face {
    font-family: "Amazon Ember";
    font-style: normal;
    font-weight: 100;
    src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
    src: local("Amazon Ember"), local("AmazonEmber"),
        /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf")
            format("truetype");
}

body {
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue";
    letter-spacing: 1;
}

.top_left_pattern_calender {
    width: 14%;
    float: left;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.top_right_calender {
    width: 16%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.monthly {
    padding: 0 9%;
}

.number {
    font-size: 115px;
    font-weight: bold;
}

.one_title {
    font-weight: 100;
    margin: 3px 0;
}

.monthly p {
    color: #000;
    font-size: 16px;
    font-weight: 200;
    line-height: 25px;
    text-align: left;
}

.monthly table {
    border-collapse: collapse;
    margin-top: 35px;
}

th {
    color: #fff;
    font-weight: bold;
}
td,
th {
    border-left: 1px solid #fff;
    text-align: left;
    padding: 8px;
    font-size: 14px;
    font-weight: 200;
    line-height: 26px;
    vertical-align: top;
    letter-spacing: 0.6px;
}

.team_title a {
    text-decoration: none;
}

.team_title a:active {
    color: #000;
}

.team_title {
    margin: 0 auto;
    display: inline-block;
}

.btn_teams {
    float: left;
    color: #000;
    font-size: 12px;
    font-weight: 400;
    /* background: #429ddb; */
    border: none;
    margin: 4vh 2vw 0 2vw;
    height: 27px;
    text-align: center;
    cursor: pointer;
}

.buttons {
    margin: 0 auto;
    display: inline-block;
    margin-top: 2vh;
}

.quaterly {
    width: 380px;
    background: #009ee1;
    color: #fff;
    height: 30px;
    float: left;
    margin: 26px 0 0 29px;
    padding: 4px 0 0 0;
    font-size: 15px;
    letter-spacing: 1.4px;
    text-decoration: none;
    text-align: center;
}

.line-monthly {
    right: 0;
    top: 238%;
    overflow-x: hidden;
    position: relative;
}

.line_break_monthly {
    width: 126%;
    right: 70px;
    position: relative;
    z-index: -1;
}
@media (min-width: 1600px) {
    .top_left_pattern_calender {
        width: 15%;
        float: left;
        position: absolute;
        top: -5%;
        left: 0;
        z-index: -1;
    }
}

@media (min-width: 1920px) {
    .monthly {
        padding: 0;
    }

    .line_break_monthly {
        width: 155.5%;
        right: 241px;
        position: relative;
        z-index: -1;
    }

    .top_left_pattern_calender {
        width: 15%;
        float: left;
        position: absolute;
        top: -7%;
        left: 0;
        z-index: -1;
    }

    .number {
        font-size: 142px;
        font-weight: bold;
    }
}

