@font-face {
  font-family: "Amazon Ember";
  font-style: normal;
  font-weight: 100;
  src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
  src: local("Amazon Ember"), local("AmazonEmber"),
    /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf") format("truetype");
}

body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue";
}

.faq {
  padding: 0 9%;
}

.faq .question {
  text-align: left;
  display: inline-block;
  font-weight: 300;
  margin-top: 0;
  font-size: 26px;
}

.faq .section_div {
  margin-top: 30px;
}

.faq p {
  color: #000;
  font-size: 16px;
  font-weight: 200;
  line-height: 26px;
  text-align: left;
}

.top_middle_faq {
  width: 16%;
  top: 44%;
  right: 0;
  z-index: -1;
  position: absolute;
}

@media (min-width: 1920px) {
  .faq {
    padding: 0;
  }
}

