@font-face {
    font-family: "Amazon Ember";
    font-style: normal;
    font-weight: 100;
    src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
    src: local("Amazon Ember"), local("AmazonEmber"),
        /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf")
            format("truetype");
}

body {
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue";
    letter-spacing: 1.4px;
}

.top_left_pattern_about {
    width: 10%;
    float: left;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.aboutl9 {
    padding: 0 9%;
}

.aboutl9 .why {
    text-align: left;
    display: inline-block;
    font-weight: 300;
    margin-top: 0;
}

.aboutl9 p {
    color: #000;
    font-size: 14px;
    font-weight: 200;
    line-height: 26px;
    text-align: justify;
}

.aboutl9 .section_div {
    margin-top: 30px;
}

.btn_Criterias_about {
    width: 130px;
    float: left;
    text-align: center;
    font-size: 16px;
    letter-spacing: 1.4px;
    text-decoration: none;
    height: 40px;
    font-weight: 300;
    margin-top: 30px;
}

.line_break_about {
    width: 93.7%;
    right: 0;
    position: absolute;
    z-index: -1;
    padding: 0;
    margin: 0;
}

.top_middle_about {
    width: 14%;
    position: absolute;
    top: 93%;
    right: 0;
    z-index: -1;
}

.mix-mode {
    mix-blend-mode: multiply;
}

.text-center {
    text-align: center;
}

.photo {
    position: absolute;
    left: 3.5%;
    width: 33.3%;
    padding: 3%;
}

.leadership_home_container_about {
    margin-top: 24%;
}

@media (min-width: 1600px) {
    .line_break_about {
        width: 93vw;
        right: 0;
        position: absolute;
        z-index: -1;
        padding: 0;
        margin: -79px 0;
    }

    .top_middle_about {
        width: 14%;
        position: absolute;
        top: 46%;
        right: 0;
        z-index: -1;
    }
}

@media (min-width: 1920px) {
    .aboutl9 {
        padding: 0;
    }
    .line_break_about {
        width: 83vw;
        right: 0;
        position: absolute;
        z-index: -1;
        padding: 0;
    }

    .leadership_home_container_about {
        margin-top: 36%;
    }
    .top_middle_about {
        width: 14%;
        position: absolute;
        top: 50%;
        right: 0;
        z-index: -1;
    }
}

