.Login {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 35vh;
}

.LoginTitle {
  font-size: 26px;
  font-family: "Helvetica Neue";
  font-weight: 200;
  padding-bottom: 40px;
}
.aliasInput {
  visibility: visible;
  width: 20vw;
  margin-bottom: 30px;
  border: none;
  font-size: 16px;
  font-family: "Helvetica Neue";
  font-weight: 300;
  letter-spacing: 1px;
  border-bottom: 1px solid #323232;
}
.aliasInput::-webkit-input-placeholder {
  color: #aaa;
  font-size: 14px;
  font-family: "Helvetica Neue";
  font-weight: 200;
  letter-spacing: 1px;
}
.aliasInput:-ms-input-placeholder {
  color: #aaa;
  font-size: 14px;
  font-family: "Helvetica Neue";
  font-weight: 200;
  letter-spacing: 1px;
}
.aliasInput::-ms-input-placeholder {
  color: #aaa;
  font-size: 14px;
  font-family: "Helvetica Neue";
  font-weight: 200;
  letter-spacing: 1px;
}
.aliasInput::placeholder {
  color: #aaa;
  font-size: 14px;
  font-family: "Helvetica Neue";
  font-weight: 200;
  letter-spacing: 1px;
}
.aliasInput:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}
.buttonLogin {
  text-align: center;
  margin-bottom: 30px;
  background: none;
  color: #303942;
  border: 1px solid #303942;
  padding: 6px 43px 6px 43px;
  -webkit-transition-duration: 0.5s; /* Safari */
  -o-transition-duration: 0.5s;
     transition-duration: 0.5s;
  cursor: pointer;
  font-size: 14px;
}
.buttonLogin:hover {
  background-color: #000;
  color: #fff;
}
.LogoLogin {
  width: auto;
  height: 50vh;
  position: absolute;
  top: -10vh;
  left: 3vw;
  /* float: left; */
  -webkit-transform: rotate(-30deg);
  -ms-transform: rotate(-30deg);
  transform: rotate(-30deg);
}



