@font-face {
    font-family: "Amazon Ember";
    font-style: normal;
    font-weight: 100;
    src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
    src: local("Amazon Ember"), local("AmazonEmber"),
        /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf")
            format("truetype");
}

body {
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue";
}

.padding-zero {
    padding: 0;
}

.g-awards {
    padding: 0 9%;
}

.g-awards .question {
    text-align: left;
    display: inline-block;
    font-weight: 300;
    margin-top: 0;
    font-size: 26px;
}

.g-awards .g-awards_section_div {
    margin-top: 30px;
}

/* .g-awards  p {
  color: #000;
  font-size: 16px;
  font-weight: 200;
  line-height: 26px;
  text-align: left;
} */

.top_middle_faq {
    width: 16%;
    top: 44%;
    right: 0;
    z-index: -1;
    position: absolute;
}

.g-award_name {
    font-size: 15px;
    letter-spacing: 1px;
    font-weight: 600;
    margin-bottom: 10px;
}

.g-award-subheading {
    font-size: 13px;
}

.g-award-title {
    font-weight: 500;
    display: inline-block;
    padding-top: 5px;
    padding-bottom: 5px;
}

.g-awards-description {
    color: #000;
    font-size: 11px;
    font-weight: 200;
    line-height: 16px;
    text-align: left;
    letter-spacing: 1px;
    word-wrap: break-word;
    margin: 10px 0 0px 0;
    /* height: 64px; */
    overflow: hidden;
    max-height: 65px;
    min-height: 65px;
}

.g-award-subheading-div {
    margin: 1px 0;
}

.image-style img {
    width: 100%;
}

.awards-tab-margin {
    padding: 10px;
}

.thumbnail {
    border: 2px solid #ddd;
}

.name-popup {
    font-size: 20px;
    margin-bottom: 8px;
}

.readmore {
    display: inline-block;
    padding: 4px 5px;
    font-size: 9px;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 1.3333333;
    margin-top: 5px;
    border-radius: 2px;
    color: #fff;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
}
.row-flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.readmore:focus {
    outline: none;
}

.winner-popup {
    font-size: 12px;
    display: inline-block;
}

.awardTable {
    border: 1px solid #000;
    text-align: left;
    padding: 8px;
    font-size: 11px;
    font-weight: 200;
    line-height: 26px;
    vertical-align: top;
    letter-spacing: 0.6px;
    color: #000;
    width: 180px;
}

@media (min-width: 1920px) {
    .faq {
        padding: 0;
    }
}

