@font-face {
    font-family: "Amazon Ember";
    font-style: normal;
    font-weight: 100;
    src: url("./fonts/Amazon_Ember_Rg.ttf"); /* IE9 Compat Modes */
    src: local("Amazon Ember"), local("AmazonEmber"),
        /* Modern Browsers */ url("./fonts/Amazon_Ember_Rg.ttf")
            format("truetype");
}

body {
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue";
}

.dropdown-select-invent {
    font-size: 26px;
    font-family: "Helvetica Neue";
    font-weight: 200;
    /* padding-bottom: 10px; */
    width: 40%;
}

.dropdown-select-invent-margin {
    margin: 10px 0;
}

/* .designForm {
    padding: 0 6vw;
} */

.designForm .section_div {
    margin-top: 30px;
}

.designForm p {
    color: #000;
    font-size: 16px;
    font-weight: 200;
    line-height: 26px;
    text-align: left;
}

.top_middle_faq {
    width: 16%;
    top: 44%;
    right: 0;
    z-index: -1;
    position: absolute;
}

.input-text {
    margin: 5px 0 10px 0;
    padding: 2px 5px;
    width: 40%;
    font-size: 14px;
    letter-spacing: 1px;
}

.input-area {
    margin: 5px 0 5px 0;
    padding: 2px 5px;
    width: 40%;
    font-size: 14px;
    letter-spacing: 1px;
}

.image-width {
    width: 100%;
}

.file-upload {
    margin: 10px 0;
}

.dropzone-button {
    color: #fff;
    background-color: #f8991d;
    border-color: #2e6da4;
    border-radius: 3px;
    display: inline-block;
    padding: 6px 10px;
    margin-bottom: 0;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    cursor: pointer;
    margin: 10px 0;
}
@-webkit-keyframes appear {
    100% {
        opacity: 1;
    }
}

@keyframes appear {
    100% {
        opacity: 1;
    }
}

.Frequency {
    margin-right: 12px;
}

@media (min-width: 1920px) {
    .faq {
        padding: 0;
    }
}
.btn_Criterias_nominate {
    float: left;
    text-align: center;
    font-size: 16px;
    letter-spacing: 1.4px;
    text-decoration: none;
    font-weight: 300;
    z-index: 2;
    padding: 5px 30px;
}



